import React, { Component } from "react";
import './PhotoGrid.css';

class Photo extends Component {
  state = {hovered: false};
  render() {
    return (
      <div>
        <img 
            src={this.props.url}
            onMouseOut = {() => this.setState({hovered: false})}
            onMouseOver = {() => this.setState({hovered: true})}
            style = {{opacity: this.state.hovered ? 0.7 : 1}}
        />
      </div>
    )
  }
}

