import React, { Component } from "react";
import {
    Route,
    NavLink,
    HashRouter
} from "react-router-dom";
import './index.css';
import Home from "./Home";
import Stuff from "./Stuff";
import PhotoGrid from "./PhotoGrid";
 
class Main extends Component {
  render() {
    return (
        // define routing region
        <HashRouter>
            <div class="bigText2">
                Monique Ouk
                <div style={{fontSize: '17px', marginTop: '13px', letterSpacing: '1px', textTransform: 'lowercase'}}>
                    <NavLink exact to="/" activeClassName="navi">portfolio</NavLink>
                    &nbsp; &nbsp;
                    <NavLink to="/stuff" activeClassName="navi">about</NavLink>
                    &nbsp; &nbsp;
                    <NavLink to="/extra" activeClassName="navi">extra</NavLink>
                   
                   
                </div>
            </div>
            <div className="content">
                <Route exact path="/" component={Home}/>
                <Route path="/stuff" component={Stuff}/>
                <Route path="/extra" component={PhotoGrid}/>
                
            </div>

        </HashRouter>
    );
  }
}
 
export default Main;